.desktop-header {
    display: flex;
    background: $sewcase-white;
    justify-content: space-between;
    padding: 0 $spacing-md;

    @media (min-width: $lg) {
        padding: 0 $spacing-lg;
    }

    .header {
        &__container {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
        &__item {
            font-size: $font-lg;
            margin-right: $spacing-md;
            @media (min-width: $lg) {
                margin-right: $spacing-lg;
            }
        }
        &-icon {
            background-image: url("../images/Sewcase_Logo_Black.png");
            background-size: contain;
            background-position: center;
            background-repeat: no-repeat;
            height: 80px;
            width: 80px;
        }
    }
}

.mobile-header {
    .header {
        display: none;

        &-mobile {
            &__navigation {
                background: rgba($color: $sewcase-black, $alpha: 0.9);
                height: 100%;
                margin: 0;
                padding: 0;
                position: fixed;
                transition: all 0.5s ease;
                transform: translateX(-100%);
                width: 100%;
                z-index: 10;
                &--open {
                    transform: translateX(0);
                }
            }
            &__list {
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: space-evenly;
                height: 80%;
                padding: 0;

                &--item {
                    font-size: 30px;
                    transition: all 0.33s ease;
                }
            }

            &__link{
                color: $sewcase-white;
            }
        }
    }
    @media (min-width: $md) {
        .header {
            align-items: center;
            background-color: rgba($color: $sewcase-black, $alpha: 0.45);
            box-shadow: 0 8px 8px -8px $sewcase-black;
            display: flex;
            height: 140px;
            justify-content: center;
            padding: 15px 10px;
            position: absolute;
            transition: all 0.5s ease;
            width: 100%;
            z-index: 1;
            &__info-text {
                font-size: 14px;
                color: $sewcase-black;
            }
            &__section {
                height: 100%;
                display: flex;
                align-items: flex-end;

                &--active {
                    display: block;
                }
            }
            &__upper-list {
                display: flex;
                flex-direction: row;
                margin-right: auto;
                padding: 0;
                & > li {
                    margin-right: 20px;
                    margin-bottom: 10px;
                }
            }

            &__navigation {
                display: block;
                left: 25%;
                &--open {
                    transition: all 1s ease;
                    display: block;
                    animation-name: headerTransition;
                    animation-duration: 1s;
                }
                @keyframes headerTransition {
                    from {
                        left: 0;
                        opacity: 0;
                    }
                    to {
                        left: 25%;
                        opacity: 1;
                    }
                }
            }
            &__link {
                font-size: 24px;
                font-weight: bold;
                color: $sewcase-black;
                transition: all 0.15s ease-in;
                &:hover {
                    text-decoration: underline;
                    font-size: 26px;
                }
            }
        }
    }
}
