.faq {
    margin-bottom: 0;
    padding: $spacing-lg 0;

    &__container {
        border: 2px solid $sewcase-black;
        margin-bottom: $spacing-xl;
    }

    &__headline {
        background: $sewcase-white;
        width: 100%;
        font-size: $font-lg;
        padding: $spacing-md;

        &--special {
            > div {
                padding-left: calc(#{$spacing-xl} - 24px);
            }

            &::before {
                content: "";
                background-image: url("../images/star.png");
                background-size: cover;
                position: absolute;
                width: 24px;
                height: 24px;
            }
        }
    }

    &__content {
        background: $sewcase-black;
        color: $sewcase-white;
        padding: $spacing-lg;
    }

    &--list {
        background: $sewcase-white;
    }
}
