// Colors
$sewcase-primary: #20bf6b;
$sewcase-primary-light: #26de81;
$sewcase-secondary: rgba(#5099cc, 0.9);
$sewcase-secondary-light: rgba(#bce3ff, 0.9);

$sewcase-black: #111;
$sewcase-grey: #eee;
$sewcase-white: #fff;

$sewcase-text-light: #e3e1de;
$sewcase-text-dark: #222;

// Responsive Layout
$md: 768px;
$lg: 992px;
$xl: 1200px;

// Spacings
$spacing-xxl: 128px;
$spacing-xl: 64px;
$spacing-lg: 32px;
$spacing-md: 16px;
$spacing-sm: 8px;

// Font Size
$font-xxl: 40px;
$font-xl: 32px;
$font-lg: 22px;
$font-md: 16px;
$font-sm: 12px;
