html,
#root,
body,
.App {
    height: 100%;
    width: 100%;
}

body {
    background-color: rgba($color: $sewcase-grey, $alpha: 0.9);
    margin: 0;
    overflow-x: hidden;
}

*,
:after,
:before {
    box-sizing: border-box;
    text-decoration: none;
    font-family: "Ubuntu";
    color: $sewcase-text-dark;
}

section {
    margin-bottom: $spacing-xl;

    @media (min-width: $md) {
        margin-bottom: $spacing-xl;
    }
}
