.parallax {
    background: $sewcase-white;
    
    &__container {
        position: relative;
        background-attachment: fixed;
        background-position: 50%;
        background-repeat: no-repeat;
        background-size: cover;
        height: 100%;
        overflow: hidden;
    }

    &--primary {
        background-image: url("../images/summonersrift.jpg");
    }
}
