#nav-icon {
    height: 45px;
    margin: 15px;
    position: fixed;
    transform: rotate(0deg);
    transition: 0.2s ease-in-out;
    width: 60px;
    z-index: 11;
    cursor: pointer;
    @media (min-width: $md){
        display:none;
    }
    span {
        background: $sewcase-white;
        border-radius: 7px;
        border-bottom: 2px solid $sewcase-black;
        display: block;
        left: 0;
        height: 7px;
        width: 100%;
        opacity: 1;
        position: fixed;
        transform: rotate(0deg);
        transition: 0.33s ease;

        &:nth-child(1) {
            top: 0px;
        }

        &:nth-child(2),
        &:nth-child(3) {
            top: 18px;
        }

        &:nth-child(4) {
            top: 36px;
        }
    }
    &.burger-menu--open {
        span {
            &:nth-child(1) {
                top: 18px;
                width: 0%;
                left: 50%;
            }
            &:nth-child(2) {
                transform: rotate(45deg);
            }
            &:nth-child(3) {
                transform: rotate(-45deg);
            }
            &:nth-child(4) {
                top: 18px;
                width: 0%;
                left: 50%;
            }
        }
    }
}

.header-burger-prevent-scroll{
    overflow: hidden
}