.conventions {
    &__list {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        flex: 1;

        &--container {
            display: flex;
            flex-direction: column;
            margin-bottom: calc(#{$spacing-lg} + 8px);

            > a {
                overflow: hidden;
            }

            &:hover {
                .conventions__list--headline, .conventions__list--content{
                    padding-left: $spacing-sm;
                    padding-right: 0;
                }
            }
        }

        &--cta {
            width: 100%;
            background: $sewcase-black;
            padding: $spacing-sm;
            display: flex;
            justify-content: center;
            color: $sewcase-white;
        }

        &--content {
            margin-bottom: $spacing-md;
            padding-right: $spacing-sm;
            transition: all 0.5s cubic-bezier(0.19, 1, 0.22, 1);
        }

        &--headline {
            font-weight: bold;
            padding: $spacing-md 0;
            padding-right: $spacing-sm;
            transition: all 0.5s cubic-bezier(0.19, 1, 0.22, 1);
        }
    }

    &__image {

        &:before {
            content: "";
            background: $sewcase-black;
            height: 200px;
            position: absolute;
            left: 5px;
            top: -10px;
            z-index: -1;
            width: 90%;
        }

        &--gc2019 {
            background-image: url("../images/ez.jpg");
            background-size: cover;
            height: 200px;
        }
        &--dokomi2018 {
            background-image: url("../images/vijinx.jpg");
            background-size: cover;
            height: 200px;
        }
    }
}
