.info {
    &__container {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;

        @media (min-width: $lg) {
            flex-direction: row;
        }
    }

    &__image {
        background-image: url("../images/vijinx.jpg");
        background-size: contain;
        background-position: center;
        background-repeat: no-repeat;
        width: inherit;
        height: inherit;

        @media (min-width: $md) and (max-width: $lg) {
            background-image: url("../images/vijinxANDleague.jpg");
        }

        &--overlay {
            background: $sewcase-black;
            width: inherit;
            height: inherit;
            position: absolute;
            left: -$spacing-md;
            top: -$spacing-md;
            z-index: -1;
        }

        &--size {
            height: 300px;
            width: 300px !important;

            @media (min-width: $md) and (max-width: $lg) {
                width: 600px !important;
            }
        }
    }

    &__content {
    }
}
