.social-media {
    > .container {
    }
    &__container {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: $spacing-md;

        @media (min-width: $md) {
            justify-content: flex-start;
        }

        @media (min-width: $lg) {
            justify-content: center;
        }

        &--header {
            margin-bottom: $spacing-md;
            padding-left: $spacing-lg;
            padding-top: $spacing-md;
        }

        > a {
            display: flex;
            justify-content: center;
            align-items: center;
            margin-bottom: $spacing-md;
            filter: brightness(75%);
            overflow: hidden;
            transition: all 0.1s ease-in;

            > img {
                transform: scale(1);
                transition: all 0.1s ease-in;
                &:hover {
                    transform: scale(1.05);
                }
            }

            &:hover {
                filter: brightness(100%);
            }
        }
    }

    &__image-box {
        margin-bottom: $spacing-md;
        > img {
            border-radius: 50%;
            height: 64px;
        }
    }

    &__info-box {
        margin: 0 0 $spacing-md $spacing-md;
        align-self: flex-end;
        font-size: $font-sm;
        max-width: 640px;
    }

    &__title {
        font-size: $font-lg;
        margin-bottom: 8px;
    }
}
