.hero-image {
    background-position: center;
    background-repeat: no-repeat;
    width: 100%;
    height: 100%;
    display: flex;
    text-shadow: 2px 2px #000;
    padding: 0;

    &--about {
        background-image: url("../images/arcade.jpg");
    }
    &--conventions {
        background-image: url("../images/fight.jpg");
    }
    &--organizers {
        background-image: url("../images/lul.jpg");
    }
    &--faq {
        background-image: url("../images/heimer.jpg");
        margin-bottom: 0;
    }

    @media (min-width: $md) {
        text-shadow: none;
        height: calc(100% - 80px);
    }

    &__block {
        width: 300px;

        @media (min-width: $md) {
            background: $sewcase-black;
        }
        @media (min-width: $xl) {
            width: 460px;
        }
    }

    &__content {
        padding: 16px 0;
        position: absolute;
        bottom: $spacing-xl;
        left: $spacing-lg;

        @media (min-width: $md) {
            bottom: calc(#{$spacing-xxl} - #{$spacing-lg});
            left: calc(#{$spacing-xxl} - #{$spacing-lg});
        }
    }

    &__headline {
        font-size: $font-xxl;
        color: $sewcase-text-light;
        text-transform: uppercase;
    }
    &__sub-headline {
        font-size: $font-xl;
        color: $sewcase-text-light;
        text-transform: uppercase;
    }
}
