.about {
    background: $sewcase-white;
    margin-bottom: 0;
    padding: $spacing-md 0 $spacing-lg;

    &__image {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: $spacing-md;
        padding: 0;
        overflow: hidden;
        width: 100%;

        &:hover {
            img {
                filter: brightness(100%) grayscale(0);
                transform: scale(1.05);
            }

            .about__image--text {
                opacity: 0;
            }
        }

        > img {
            width: inherit;
            filter: brightness(50%) grayscale(50%);
            transition: all 0.15s ease-in-out;
        }

        &--text {
            position: absolute;
            color: white;
            opacity: 1;
            font-size: 28px;
            transition: all 0.33s ease-in-out;
        }

        &-container {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            @media (min-width: $lg) {
                justify-content: space-around;
                flex-direction: row;
            }
        }
    }
}
